.bill-container {
    padding: 1.5rem;
    border-radius: 1.5rem 1.5rem 0 0;
    position: relative;
    top: -1rem;
    background-color: white;
}

@media (min-width: 700px) {
    .bill-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-radius: 1.5rem;
    }
    .desktop-left{
        width: 48%;
    } 
    .desktop-rigth{
        width: 48%;
        align-self: flex-start;
    }

    .total-container {
        margin-top: 0;
        height: 435px;
    }

    .tip-percent-container__box {
        padding: .5rem 1rem;
    }
}