.tip-percent-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.tip-percent-container__box {
    /* flex-grow: 1; */
    width: 47%;
    padding: 1rem;
    text-align: center;
    background-color: hsl(183, 100%, 15%);
    margin-bottom: 1rem;
    border-radius: .3rem;
    color: white;
    
}
.selected {
    background-color: hsl(172, 67%, 45%);
}

.tip-percent-container__box:hover {
    cursor: pointer;
}

.custom {
    width: 48%;

}
.custom input {
    font-size: 1.4rem;
    font-weight: bold;
    background-color: hsl(189, 41%, 97%);
    width: 100%;
    border: 2px solid transparent;
    color: hsl(184, 14%, 56%);
    text-align: end;
}