.total-container {
    background-color: hsl(183, 100%, 15%);
    padding: 1rem;
    border-radius: 1rem;
    margin-top: 2rem;
}

.total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1rem 0;
}
.total-title h3, .total-title h4 {
    margin: 0;
}
.total-title h3 {
    color: white;
    font-size: 1rem;
}

.total-title h4 {
    color: hsl(184, 14%, 56%);
    font-size: .9rem;
}

.total-number {
    color: hsl(172, 67%, 45%);
    font-size: 2rem;
}


.total-reset button {
    width: 100%;
    padding: 1rem;
    border-radius: .5rem;
    border: none;
    background-color: hsl(172, 67%, 45%);
    color: hsl(183, 100%, 15%);
    font-weight: bold;
    font-size: 1.2rem;
    letter-spacing: 1px;
    cursor: pointer;
}