.bill-input-container {

}

.bill-input {
    width: 100%;
    text-align: end;
    font-size: 2rem;
    border: 2px solid transparent;
    background-color: hsl(189, 41%, 97%);
    
    border-radius: .5rem;
    padding: 1rem;
    height: 3rem;
    
}
input:hover {
    border: 2px solid hsl(172, 67%, 45%) !important;
    cursor: pointer;
}

